.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
    overflow: hidden;
}


.tui-pagination .tui-page-btn {
    display: inline-block;
    position: relative;
    width: 28px;
    padding: 8px 0 6px;
    margin-left: 0;
    color: #333;
    font-size: 12px;
    font-weight: normal;
    line-height: 1;
    text-decoration: none;
    vertical-align: middle;
    border: 1px solid #ddd;
    border-width: 1px 0;
}

.tui-pagination .tui-page-btn:hover {
    background-color: $AccentColorOrange;
    higth: 40px;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
    color: #fff;
    background: #4b96e6;
    border-color: #4b96e6;
    cursor: default;
}

.tui-pagination .tui-is-selected:hover {
    background-color: #4b96e6;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
    margin-left: 0
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
    border-left: 1px solid #ddd
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
    border-right: 1px solid #ddd
}

.tui-pagination .tui-first-child.tui-is-selected {
    border-left: 1px solid #4b96e6
}

.tui-pagination .tui-last-child.tui-is-selected {
    border-right: 1px solid #4b96e6
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    height: 26px;
    padding: 0;
    font-size: 0;
    line-height: 26px;
    white-space: nowrap;
    font-weight: normal;
    border: 1px solid #ddd;
}

.tui-pagination .tui-prev-is-ellip {
    border-right: 0
}

.tui-pagination .tui-next-is-ellip {
    border-left: 0
}

.tui-pagination .tui-first+.tui-prev,
.tui-pagination .tui-next+.tui-last {
    border-left: 0
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
    background: url(data:image/png;base64,) no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    padding: 0;
    line-height: 1;
}

.tui-pagination .tui-prev {
    margin-right: 10px;
}

.tui-pagination .tui-next {
    margin-left: 10px;
}

.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px
}

.tui-pagination .tui-ico-prev {
    width: 6px;
    height: 10px;
    background-position: -26px 0px
}

.tui-pagination .tui-ico-next {
    width: 6px;
    height: 10px;
    background-position: -13px -24px
}

.tui-pagination .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px
}

.tui-pagination .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 16px;
    vertical-align: top;
    background-position: 0px -24px
}

.tui-pagination .tui-is-disabled:hover {
    background: #fff;
    cursor: default
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
    background-position: -26px -12px
}

.tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px
}

.tui-pagination .tui-is-disabled .tui-ico-next {
    background-position: -21px -24px
}

.tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px
}

.tui-page-inputs {
    display: inline-block;
    vertical-align: top;
}

.tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 10px;
    height: 12px;
    padding: 8px 0 6px;
    font-size: 12px;
    text-align: center;
    color: #333;
    border: 0;
    outline: none;
}

.tui-page-inputs .tui-page-current {
    color: #4b96e6
}

.tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 8px 0 6px;
    vertical-align: top
}







#pagination {
    flex-shrink: 0;
    margin-top: 40px;
    

    @media (min-width: 768px) {
        margin-top: 60px;
    }

    .tui-page-btn {
        border: none;
        line-height: 1.2px;
        font-weight: 500;
        color: $btnBlackColor;

        &:hover {
            background-color: $backGroundGray;
        }

        @media (min-width: 768px) {
            font-size: 12px;
            margin-right: 15px;
        }

        @media (min-width: 1280px) {
            font-size: 12px;
            margin-right: 20px;
        }
    }
        .tui-page-btn:not(:last-child) {
            @media (min-width: 768px) {
                    margin-right: 15px;
                }
            
                @media (min-width: 1280px) {
                    margin-right: 20px;
                }
        }

    .tui-is-selected {
        width: 25px;
        height: 25px;
        display: inline-flex;
        background-color: $AccentColorOrange;
        color: $btnWhiteColor;
        border-radius: 5px;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        vertical-align: middle;

        &:hover {
            background-color: $AccentColorOrange;
        }

        @media (min-width: 768px) {
            width: 40px;
            height: 40px;
        }
    }

    .tui-ico-first,
    .tui-ico-last {
        margin-top: 8px;
    }

    .tui-ico-first {
        @media (min-width: 768px) {
            margin-right: 10px;
        }
    }

    .tui-ico-last {
        @media (min-width: 768px) {
            margin-left: 10px;
        }
    }

    .tui-prev {
        margin-right: 5px;

        @media (min-width: 768px) {
            margin-right: 10px;
        }
    }

    .tui-next {
        margin-left: 5px;

        @media (min-width: 768px) {
            margin-left: 10px;
        }
    }

    .tui-prev,
    .tui-next {
        width: 25px;
        height: 25px;

        background-color: $backGroundGray;
        border-radius: 5px;
        position: relative;
        transition: background-color $main-transition-duration;

        &:hover {
            color: $textColorWhite;
            background-color: $backGroundGray;
        }

        @media (min-width: 768px) {
            width: 40px;
            height: 40px;
        }
    }

    .tui-ico-next,
    .tui-ico-prev {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.tui-pagination {
    text-align: center;
    height: 40px;
}

